import React from 'react'
import { Link } from 'react-router-dom'
import Slider from "react-slick";
import Navbar2 from '../layout/Navbar2'
class DituApp extends React.Component {
    render() {
        var settings = {
            dots: false,
            accessibility: true,
            slidesToShow: 4,
            slidesToScroll: 1,
            autoplay: true,
            autoplaySpeed: 2000,
            infinite: true,
            arrows: true,
            responsive: [
                {
                    breakpoint: 1024,
                    settings: {
                        slidesToShow: 2,
                        slidesToScroll: 2,
                        infinite: true,
                        dots: true
                    }
                },
                {
                    breakpoint: 600,
                    settings: {
                        slidesToShow: 1,
                        slidesToScroll: 1
                    }
                },
                {
                    breakpoint: 480,
                    settings: {
                        slidesToShow: 1,
                        slidesToScroll: 1
                    }
                }
                // You can unslick at a given breakpoint now by adding:
                // settings: "unslick"
                // instead of a settings object
            ]
        };
        return (
            <div id="content">
                <Navbar2/>
                <div className="half">
                </div>
                <div className="card proj-card">
                    <div className="proj-cont">
                        <div className="row">
                            <h2 className="center"><b>DITU Student App</b></h2>
                            <div className="slide-obj">
                                <Slider  {...settings}>
                                    <div className="slide-item">
                                        <img src="https://lh3.googleusercontent.com/ZIElhgsIUggQm_2iy4TjTQxbI35ZaB8igj4TnbPAbjyviAZDFpEKPGShcdkQZHDKC8Y=w1536-h754-rw" />
                                    </div>
                                    <div className="slide-item">
                                        <img src="https://lh3.googleusercontent.com/wmaa6MjVOg4Rf1NNQQmfdIK1aH8w54b7KaW2Ajgw5eTqaDBVLWgL0PHvyZudskZ5Jpw=w1536-h754-rw" />
                                    </div>
                                    <div className="slide-item">
                                        <img src="https://lh3.googleusercontent.com/KyEOX5MmjgMKY2wwTlEYVAsAspZLbBErsORjbxgWvwQYdfF88eLl_HoMAiXfiLqElaml=w1536-h754-rw" />
                                    </div>
                                    <div className="slide-item">
                                        <img src="https://lh3.googleusercontent.com/IvcMgqd348sFSyGkyMbD6hujBhKRoLMGxWFNttlyXkNpTevckiPA-oaSUbrAojXkMVU=w1536-h754-rw" />
                                    </div>
                                    <div className="slide-item">
                                        <img src="https://lh3.googleusercontent.com/6lWZATxT2J6c5y2zcYfX9JnhacLAzMzFXhpRSluXK78MRRL-mV-7D92_k_Q623194w=w1536-h754-rw" />
                                    </div>
                                    <div className="slide-item">
                                        <img src="https://lh3.googleusercontent.com/f0GldKr57AUpTECyBsiRIzwLvJkOyC8Owj6jVw61gqraEbyEaI67GzF9TiWQHz2hrLp0=w1536-h754-rw" />
                                    </div>
                                </Slider>
                            </div>
                            <div className="col s12 m4">

                                <h4>Description</h4>
                            </div>
                            <div className="col s12 m8">
                                <h6>This app was created to help DIT University, Dehradun students to make their life little bit easier.<br /><br />This App has following features<br /><br />-DIT News - Now get all the latest announcements from DIT University directly in the app<br /><br />-Timetable - Now you don't need to find that timetable pic you took in your gallery or create a timetable and paste in your room<br /><br />-Attendance Manager - Now get to know all the number of days you can bunk to make sure your attendance stay above 75%<br /><br />-Attendance Calculator<br /><br />-Old Papers<br /><br />-Easy to use SAP(Save password once then you wont need to remember its password) with direct shortcut links<br /><br />-Chat! Now chat, share images and files with your classmates from the app.<br /><br />-Create/Edit your own timetable.(Only require one time addition then rest classmates can easily register with already created table)<br /><br />-Events - All club events in one place<br /><br />-Sharezone - Now share files or any important message with all your batchmates<br /><br />-My Files - Now you don't need to store your codes, screenshots to pendrive or emails.. Check the app to know more<br /><br /></h6>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col s12 m4">
                                <h4>Skills Used</h4>
                            </div>
                            <div className="col s12 m8">
                                <h6>Java(Core), Android, XML, PHP, MySQL Firebase</h6>
                                <h6>PHP is used for retriving and sending data(text,images,docs) to server</h6>
                                <h6>Firebase -  for Google Authentication, Realtime Database for Chat, Storage for Profile Pic, And FCM for push notifications</h6>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col s12 m4">
                                <h4>Get It</h4>
                            </div>
                            <div className="col s12 m8">
                                <a href="https://play.google.com/store/apps/details?id=com.ditu.yash.timetableditu" target="_blank"><img src="https://upload.wikimedia.org/wikipedia/commons/thumb/c/cd/Get_it_on_Google_play.svg/1280px-Get_it_on_Google_play.svg.png" width="200px" target="_blank"/></a>
                            </div>
                        </div>
                    </div>


                </div>

            </div>
        )
    }
}

export default DituApp